import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

//import PropTypes from "prop-types";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };

  getMovies = async () => {
    const {
      data: {
        data: { movies }
      }
    } = await axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    );
    //console.log(movies);
    this.setState({ movies: movies, isLoading: false }); //or just {movies}, instead of {movies: movies}
  };

  componentDidMount() {
    this.getMovies();
  }

  //renderMovies() {}

  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map(movie => (
              //console.log(movie);
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                genres={movie.genres}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
              />
            ))}
          </div>
        )}
      </section>
    ); //return
  } //render
} //class

export default App;
